<template>
  <div class="carouselOut">
    <div class="carouselcontent">
      <div class="carouselBox" @mouseover="clearTimer" @mouseout="play">
        <transition-group :name="slide" tag='ul' class="wrap-ul">
            <li v-for="(wrap ,index) in wraps" :key="index" v-if="action == index">
              <a :href="wrap.to">
                <img :src="wrap.src" :alt="wrap.alt" :title="wrap.alt"/>
              </a>
            </li>
        </transition-group>
        <ul class="paging-ul">
          <li v-for="(pag ,index) in wraps" :class="{active: action == index}" :key="index" @click="activing(index)"></li>
        </ul>
        <div class="lastPic" @click="last"></div>
        <div class="nextPic" @click="next"></div>
      </div>
      <div class="carouselDown">
        <h3></h3>
        <ul class="carouselDown-ul">
          <li v-for="title in titles">
            <a href="#">
              {{title.name}}
            </a>
          </li>
        </ul>
        <a class="more" href="#"> ></a>
      </div>
    </div>
    <ul>
      <li :is="'Chartpart'" v-for="msg in msgs" :somejock="msg.Msg" :someIMG="msg.Src"></li>
    </ul>

    <Talksome v-on:bus="addinfo"></Talksome>
  </div>
</template>

<script>

import MsgBus from '@/components/msgbus.js'
import Chartpart from './chartpart'
import Talksome from './talksome'

export default {
  name: 'carousel',
  components:{
    Chartpart,
    Talksome
  },
  data () {
    return {
      test:'',
      action: '0',
      timer: null,
      slide: 'slideL',
      wraps:[
            // src:require('URL')
            {name:'1',src:require('../assets/broadcast/1651.jpg'),alt:'',to:"#"},
            {name:'2',src:require('../assets/broadcast/1652.jpg'),alt:'',to:"#"},
            {name:'3',src:require('../assets/broadcast/1653.jpg'),alt:'',to:"#"},
            {name:'4',src:require('../assets/broadcast/1654.jpg'),alt:'',to:"#"}
          ],
      titles:[
            {name:'',to:"#"},
            {name:'',to:"#"},
            {name:'',to:"#"},
            {name:'',to:"#"}
          ],
      msgs:[
            {Msg:'   ',Src:require('../assets/user/badad933c895d143ce72575478f082025aaf073a.jpg')},
            {Msg:'IP James Corden  Domhnall Gleeson  Rose Byrne ',Src:require('../assets/user/ui3WO.jpg')},
            {Msg:'9*170100B',Src:require('../assets/user/a9d3fd1f4134970ab5d9159995cad1c8a6865dd9.jpg')}
        ]
    }
  },
  methods:{
    activing: function(index){
      if (index < this.action) {
        //indexaction<transition>nameclass
        this.slide = 'slideR'
      }
      this.action = index
    },
    Autoplay: function(){
      this.action++;
      if (this.action >= this.wraps.length) {
        this.action = 0
      }
    },
    play: function(){
      this.timer = setInterval(this.Autoplay, 2500)
    },
    clearTimer: function(){
      clearInterval(this.timer)
    },
    last: function(){
      this.slide = 'slideR';
      this.action--;
      if (this.action < 0) {
        this.action = this.wraps.length-1;
      }
    },
    next: function(){
      this.action++;
      if (this.action >= this.wraps.length) {
        this.action = 0
      }
    },
    addinfo: function(info){
        this.msgs.unshift({
          'Msg': info.Msg,
          'Src': info.Src
      });
        console.log(this.msgs)
    }
  },
  created: function(){
    this.play();
  },
  updated: function(){
    this.slide = 'slideL'
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  ul{
      list-style: none;
    }

  .carouselOut{
    width: 600px;
    margin-top: 15px;
    background: #f1e7ce;
  }
  .carouselcontent{
    height: 195px;
  }

  .carouselDown{
    width: 100%;
    box-sizing: border-box;
    background: #fff;
  }
  .carouselBox{
    width: 100%;
    height: 150px;
    overflow: hidden;
    position: relative;
  }
  .wrap-ul{
    width: 100%;
    height: 150px;
    display: flex;
    flex-direction: row;
    position: relative;
  }
  .wrap-ul li{
    position: absolute;     /**/
  }
  .paging-ul{
    width: 32px;
    height: 12px;
    padding: 0 15px;
    border-radius: 6px;
    background: #333;
    opacity: 0.8;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    /*transform: translate(300px, -16px); *//**/
    position: relative;
    left: 295px;
    top: -16px;
  }
  .active{
    background: #666!important;
  }
  .lastPic{
    width: 20px;
    height: 30px;
    background: url('../assets/arrow.png') no-repeat;
    position: absolute;
    top: 60px;
    cursor: pointer;
  }
  .nextPic{
    width: 20px;
    height: 30px;
    background: url('../assets/arrow.png') -30px 0 no-repeat;
    position: absolute;
    right: 0;
    top: 60px;
    cursor: pointer;
  }
  .paging-ul li{
    width: 6px;
    height: 6px;
    border-radius: 3px;
    background: #fff;
    margin-left: 2px;
    cursor: pointer;
  }
  .slideL-enter-active {  
    transform: translateX(0);  
    transition: all 1.5s ease;  
  }  
  .slideL-leave-active {  
    transform: translateX(-100%);  
    transition: all 1.5s ease;  
  }  
  .slideL-enter {  
    transform: translateX(100%);  
  }  
  .slideL-leave {  
    transform: translateX(0);  
  } 

  .slideR-enter-active {  
    transform: translateX(0);  
    transition: all 1.5s ease;  
  }  
  .slideR-leave-active {  
    transform: translateX(100%);  
    transition: all 1.5s ease;  
  }  
  .slideR-enter {  
    transform: translateX(-100%);  
  }  
  .slideR-leave {  
    transform: translateX(0);  
  }




  .carouselDown{
    width: 100%;
    height: 44px;
    box-sizing: border-box;
    padding: 8px 10px;
    font-size: 12px;
    color: #333;
    display: flex;
    flex-direction: row;
  }
  .carouselDown h3{
    width: 48px;
    height: 28px;
    margin: 0;
    padding-right: 10px;
    line-height: 28px;
    color: #666;
    font-size: 100%;
    font-weight: 400;
    cursor: text;
  }
  .carouselDown ul{
    width: 480px;
    height: 28px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
  }
  .carouselDown ul li{
    width: 48px;
    height: 24px;
    line-height: 24px;
    padding-left: 15px;
    padding-right: 20px;
    margin-right: 6px;
    border-radius: 15px;
    background: url('../assets/icon.png') right -1174px no-repeat #f6f6f6;
  }
  .carouselDown ul li:hover{
    background: url('../assets/icon.png') right -1174px no-repeat #f1e7ce;
  }
  .carouselDown ul li a{
    color: #b69e88;
  }
  .more{
    display: block;
    width: 37px;
    height: 28px;
    line-height: 28px;
    color: #b69e88;
  }
</style>


